.footer {
  width: 100%;
  height: 80%;
  margin-top: 150px;
  background-color: var(--footerBackground);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  padding: 50px;
}

.input-container__input {
  width: 400px;
  height: 50px;
  padding: 5px;
  margin: 0 0 20px 0;
  border-radius: 10px;
  outline: none;
  font-size: 1.2vw;
  font-family: "Yantramanav", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.form__textarea {
  width: 100%;
  height: 200px;
  padding: 5px;
  margin: 0 0 20px 0;
  border-radius: 10px;
  outline: none;
  resize: none;
  font-size: 1.2vw;
  font-family: "Yantramanav", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.form__button {
  width: 100%;
  padding: 15px;
  border-radius: 10px;
  border: none;
  background-color: white;
  font-size: 1.2vw;
  font-family: "Yantramanav", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.input-container__input:focus {
  border: 5px solid var(--activeColor);
}

.form__textarea:focus {
  border: 5px solid var(--activeColor);
}


.policy-container {
  margin-top: 50px;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
}

.policy-label {
  color: #fff;
}

.policy-input {
  appearance: none;
  -webkit-appearance: none;
}

.policy-input {
  width: 25px;
  height: 25px;
  border: 3px solid var(--activeColor);
  border-radius: 0.25rem;
  background-color: transparent;
  cursor: pointer;
  transition: all .5s;
  margin: 0 10px 0 0;
}

.policy-input:checked {
  background-color: var(--activeColor);
  border-color: var(--activeColor);
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='m6 10 3 3 6-6'/%3e%3c/svg%3e");
  background-size: cover;
  background-position: center;
}

.policy__link {
  color: var(--activeColor);
  text-decoration: none;
  cursor: pointer;
  transition: all .5s;
}